.Dialogs {
  display: grid;
  grid-template-columns: 4fr 10fr;
  gap: 10px;
  border-radius: 4px;
  /* background-color: #aed6f7; */
  border-radius: 10px;
  height: 500px;
  overflow: hidden;
  box-shadow: 0px 0px 2.5px -1px black;
}

.messagelist {
  border-radius: 3px;
}

.itemD::-webkit-scrollbar {
  background-color: rgb(120, 139, 120, 0.6);
  width: 0px;
}

.redBorder {
  box-shadow: 0 0 3px 1px red;
}

.itemD::-webkit-scrollbar-thumb {
  background-color: rgb(77, 126, 77);
}

.itemD {
  overflow-y: scroll;
  max-height: 357px;
  padding-left: 10px;
}

.messagelist img {
  width: 60px;
  height: 60px;
  border-radius: 30px;
  float: right;
}

.dialogMessage {
  margin-top: 0px;
  border-radius: 10px;
  width: 650px;
}

/* .dialog_container {
  width: 200px;
  outline: hidden;
  word-wrap: break-word;
} */

.dialogMessage .img img {
  margin-left: 10px;
  width: 15px;
  cursor: pointer;
}

.dialogMessage .img {
  opacity: 0.2;
  float: right;
}

.dialogMessage .img:hover {
  opacity: 0.8;
}

.dialogMessage .img:active {
  opacity: 1;
}

.dialogMessage div {
  padding: 12px;
  letter-spacing: 1px;
  border-radius: 8px;
  margin-left: 2px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.dialog {
  background-color: rgb(255, 255, 255, 0.7);
  border-radius: 3px;
  position: relative;
}

.Dialogs .item {
  padding: 4px;
  overflow: hidden;
}

.item .names {
  float: left;
  margin-left: 20px;
}

.active .dialogStyles {
  background-color: rgb(223, 223, 223);
  font-weight: bold;
  color: rgb(3, 3, 3, 0.8);
  opacity: 1;
  box-shadow: 0px 0px 0px 0.5px rgb(0, 0, 0, 0.5);
}

.dialogStyles {
  margin-bottom: -3px;
  opacity: 0.5;
  margin-top: -4px;
  box-shadow: 0px 0px 0px 0.5px rgb(0, 0, 0, 0.5);
  margin-left: -5px;
  margin-right: -5px;
}

.dialog2 {
  bottom: 0;
  width: 100%;
  overflow: hidden;
  position: absolute;
  z-index: 4;
}

.messagelist {
  color: rgba(28, 51, 70, 0.6);
}

.messagelist a:active {
  color: black;
}

.textareaButton {
  margin: 10px;
  display: flex;
  padding: 7px 7px 0px 7px;
  border-radius: 4px;
  margin-top: 20px;
}

.textareaButton button {
  margin-left: 6px;
  padding: 3px 19px 3px 19px;
  margin-top: 1px;
  margin-bottom: 6px;
  border-radius: 17px;
  font-size: 12px;
  background-color: rgb(104, 104, 255);
}

.textareaButton button:hover {
  margin-left: 6px;
  background-color: rgb(121, 121, 255);
  color: white;
}

.textareaButton textarea {
  outline: none;
}

.Dialogs .showMessage,
.lastmessage {
  margin-left: 20px;
  text-align: center;
  display: inline-block;
  color: rgb(85, 85, 138);
  cursor: pointer;
}

.dialogMessage .span {
  box-shadow: 0px 0px 2.5px -1px black;
  background-color: rgb(255, 255, 255);
  padding: 5px 10px 5px 10px;
  border-radius: 2px;
  color: black;
  display: inline-block;
  max-width: 370px;
  font-size: 16px;
  margin-left: 30px;
}

.dialogMessage .span img {
  width: 25px;
  height: 25px;
  margin-top: -25px;
  margin-left: -40px;
  margin-right: 10px;
  border-radius: 50%;
}

.mymessage {
  box-shadow: 0px 0px 2.5px -1px black;
  background-color: rgb(221, 221, 238);
  padding: 5px 10px 5px 10px;
  border-radius: 2px;
  outline: hidden;
  word-wrap: break-word;
  max-width: 370px;
  display: inline-block;
  position: relative;
  font-size: 16px;
  margin-left: 30px;
}

.curentDialog {
  background-color: rgb(250, 250, 250);
  height: 50px;
  /* position: absolute; */
  width: 100%;
  text-align: center;
}

.curentDialog img {
  width: 40px;
  height: 40px;
  border-radius: 30px;
  float: right;
  margin-top: 5px;
  margin-right: 5px;
}

.curentDialog .name {
  margin-top: 13px;
  display: inline-block;
  font-weight: bold;
}

.mymessage img {
  width: 25px;
  height: 25px;
  margin-top: -25px;
  margin-left: -40px;
  margin-right: 10px;
  border-radius: 50%;
}

.dialogStyles {
  overflow: hidden;
  padding: 4px;
}

.dialogStyles div {
  margin-top: 13px;
  color: rgb(0, 0, 0);
  opacity: 0.6;
}

.dialogStyles .lastmessage {
  color: rgb(0, 0, 0, 0.3);
}
